'use client';

import { useEffect } from 'react';
import { useQuery } from '@tanstack/react-query';
import { tasksApi } from '@/services/api';

export default function TestQueryPage() {
  const { data, isLoading, error, isError, isFetching, isSuccess } = useQuery({
    queryKey: ['test-tasks'],
    queryFn: async () => {
      console.log('üîÑ Starting API request...');
      try {
        const response = await tasksApi.getAll();
        console.log('‚úÖ API response received:', response.data);
        return response.data;
      } catch (err) {
        console.error('‚ùå API request failed:', err);
        throw err;
      }
    },
    staleTime: 0,
    gcTime: 0,
    retry: false,
    refetchOnWindowFocus: false,
  });

  useEffect(() => {
    console.log('üèóÔ∏è Component render state:', {
      isLoading,
      isFetching,
      isSuccess,
      isError,
      hasData: !!data,
      error: error?.message || 'none'
    });
  }, [isLoading, isFetching, isSuccess, isError, data, error]);

  if (isLoading) {
    return <div className="p-4">‚è≥ Loading tasks...</div>;
  }

  if (error) {
    return <div className="p-4 text-red-500">‚ùå Error: {String(error)}</div>;
  }

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold mb-4">‚úÖ Test Query Results</h1>
      <div className="mb-4">
        <p>Status: {isSuccess ? '‚úÖ Success' : '‚è≥ Loading'}</p>
        <p>Data count: {data?.length || 0}</p>
      </div>
      <pre className="bg-gray-100 p-4 rounded text-sm overflow-x-auto">
        {JSON.stringify(data, null, 2)}
      </pre>
    </div>
  );
}